export let events = new class Events {
    constructor() {
        this.computeTypeListeners = [];
    }
    onComputeType(f) {
        this.computeTypeListeners.push(f);
    }
    dispatchComputeType(current, prev) {
        for (let f of this.computeTypeListeners) {
            f(current, prev);
        }
    }
};
